
main {
  text-align: center;
}
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.fade-leave {
}
.fade-leave-active {
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
  opacity: 0;
}



