
#loadingSpinner{
  top:10px;
  width: 52px !important;
}
.slider-wrapper input {
  width: 150px;
  height: 20px;
  margin: 0;
  -webkit-transform-origin: 75px 75px;
          transform-origin: 75px 75px;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.slider-wrapper {
  display: inline-block;
  width: 20px;
  height: 150px;
  padding: 0;
}
#dc-audio {
  display: none;
}
#dc-audio-container {
  width: 100%;
}
.audio-controls {
  float: left;
}
#dc-player {
  padding-top: 5px;
}

/* #progress-slider {
  top: -8px
} */
#poster {
  height: 50px
}

/* #progress {
  height: 50px;
} */
#left {
  margin-top: 7px;
  float: left;
}
#middle {
  margin-left: 230px;
}
@media only screen and (min-width: 600px){
#middle {
    margin-right: 100px;
}
}
#right {
  float: right;
}





